.btn {
    background-color: #272525;
    width: 150px;
    height: 20px;
    margin-top: 5em;
    margin-left: 38em;
    color: #fff;
    padding: 0.7em 1.2em;
    text-decoration: none;
    transition: .5s;
    cursor: pointer;
    
    border-radius: 5px;
}

.btn:hover {
    color: #ffbb44;
}