.conteudo {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   min-height: 100vh;
   background-color: rgb(216, 218, 219);
   
}

.conteudo img {
   width: 200px;
   margin: 2em 0;
   border-radius: 25%;
   flex-shrink: 0;
}

.image_item {
   display: flex;
   align-items: center;
}

.image_item img {
   margin-right: 20px;
   /* Espaçamento entre a imagem e o texto */
}

.image_item span {
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   font-size: 1.5em;
   color: #222;
   width: 1000px;
}

.quebra_linha {
   white-space: pre-line;
   text-align: justify;
   text-indent: 2em;
}

.conteudo h1 {
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   font-size: 1em;
   width: 170px;
   justify-content: center;
   margin-top: 100px;
   margin-bottom: 50px;
   align-items: center;
   border: #222;
   border-radius: 15px;
   background-color: #222;
   color: aliceblue;
   padding: 0 1em;
}